<template>
  <div id="tablet">
    <div class="queryBox">
      <QueryBox :formArr="queryFrom" @query="getQueryData" style="width: calc(100% - 75px)" id="ledgerQuery"></QueryBox>
      <div class="queryBtns">
        <Button type="success" size="small" class="btn-add" icon="md-add" @click.stop="openAddModal">新建</Button>
      </div>
    </div>
    <div class="content_box">
      <Table border stripe :loading="loading" :columns="columns1" :data="data1" highlight-row :maxHeight="tableHeight"></Table>
      <div class="page_box">
        <Page :total="total" :page-size="pageSize" :current.sync="pageNum" @on-change="pageChange" show-total/>
      </div>
    </div>
    <div class="extract_box" :class="{addActive:addActive}">
      <extracTableMap ref="extracTableMap" @signOut="signOut"></extracTableMap>
    </div>
    <Modal v-model="modal1"
           title="新建人类活动提取任务"
           :mask-closable="false">
      <Form :model="addForm" :label-width="150" ref="addForm" :rules="addFormRules" style="width: 85%; margin: 20px auto 0;position: relative" >
        <FormItem prop="name" label="人类活动提取任务名称">
          <Input v-model="addForm.name" placeholder="请填写人类活动提取任务名称，最少四个字符"></Input>
        </FormItem>
        <FormItem prop="url" label="遥感影像文件" style="position: relative">
          <i style="position: absolute;left: -95px;top: 2px;color: #ff0000;font-size: 15px;">*</i>
          <Input v-model="addForm.url" style="display: none"></Input>
          <Upload
            class="upload-demo"
            action="/epr/system/upload/fileUpload"
            name="file"
            :data="{dirId: 'nr_temp'}"
            :multiple="false"
            :on-success="uploadSuccess"
            :on-progress="handleProgress"
            accept=".zip"
            ref="upload">
            <Button icon="ios-cloud-upload-outline">选择影像文件</Button>
            <div class="el-upload__tip" slot="tip" style="color: orange;position: absolute;top: 0px;right: 45px;">只能上传zip文件</div>
            <div v-if="showStatus" style="color: #ed4014;position: absolute;bottom: -28px;z-index: 10;">遥感影像文件不能为空</div>
          </Upload>
        </FormItem>
        <FormItem prop="createDate" label="数据日期">
          <DatePicker type="date" placeholder="请选择创建时间" v-model="addForm.createDate" style="width: 100%"></DatePicker>
        </FormItem>
        <FormItem prop="remark" label="人类活动提取任务说明">
          <Input type="textarea" v-model="addForm.remark" placeholder="请填写人类活动提取任务说明"></Input>
        </FormItem>
        <Spin fix v-if="spinShow" style="z-index:1000">
          <Icon type="ios-loading" size=18 class="demo-spin-icon-load"></Icon>
          <div>遥感影像文件上传中</div>
        </Spin>
      </Form>
      <div slot="footer">
        <Button @click="modal1=false">取消</Button>
        <Button type="primary" @click="submitAddForm" :loading="buttonLoading">自动识别</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { getRemoteList, addRemoteTask, deleteRemoteTask } from '@/api/remoteSensing'
import { insertImagerTemp, fileUpload, listImagerTemp } from '@/api/automation'
import QueryBox from '@/components/publicQuery'
import extracTableMap from './automationTable_map'
import { renderTime } from '../../../utils/common'
import arcgisPackage from '../../../utils/arcgisTools'
import config from '../../../setting/mapConfig'
/* 新增的提取任务表格的验证 */
const ADDFORMRULES = {
  name: [
    { required: true, message: '人类活动提取任务必须填写', trigger: 'blur' },
    { type: 'string', min: 4, message: '人类活动提取任务名称为4-20个字符组成', trigger: 'blur' },
    { type: 'string', max: 20, message: '人类活动提取任务名称为4-20个字符组成', trigger: 'blur' }
  ],
  createDate: [
    { required: true, type: 'date', message: '创建时间不能为空', trigger: 'blur' }
  ],
  // url: [
  //   { required: true, type: 'date', message: '遥感影像文件不能为空', trigger: 'click' }
  // ]
}
export default {
  name: 'extracTable',
  data () {
    return {
      /* 查询框查询条件 */
      queryData: {},
      /* 表格高度 */
      tableHeight: Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40,
      /* 表格数据 */
      columns1: [
        {
          title: '序号',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + (this.pageNum - 1) * this.pageSize + 1)
          }
        },
        {
          title: '人类活动提取任务名称',
          align: 'center',
          minWidth: 200,
          key: 'name'
        },
        {
          title: '人类活动提取任务说明',
          align: 'center',
          minWidth: 250,
          key: 'remark'
        },
        {
          title: '数据日期',
          align: 'center',
          width: 120,
          key: 'updateDate'
        },
        {
          title: '遥感影像名称',
          align: 'center',
          width: 300,
          key: 'zipUrl',
          render: (h, params) => {
            if (params.row.zipUrl === 'null') {
              return h('span', '')
            } else {
              return h('span', params.row.zipUrl)
            }
          }
        },
        /* {
          title: '人类活动块数',
          align: 'center',
          width: 120,
          key: 'plaqueNumber'
        },
        {
          title: '人类活动面积（公顷）',
          align: 'center',
          width: 180,
          key: 'area'
        }, */
        {
          title: '操作',
          key: 'address',
          width: 230,
          align: 'center',
          render: (h, params) => {
            let temp = null
            if (params.row.flag) {
              temp = h('Button', {
                props: {
                  type: 'success',
                  size: 'small',
                  icon: 'ios-loading'
                },
                style: {
                  marginRight: '10px',
                  background: 'rgb(106,101,204)',
                  borderColor: 'rgb(16,186,204)',
                  cursor: 'progress'
                }
              }, '正在识别中')
            } else {
              temp = h('Button', {
                props: {
                  type: 'success',
                  size: 'small',
                  icon: 'ios-map'
                },
                style: {
                  marginRight: '10px',
                  background: 'rgb(16,186,204)',
                  borderColor: 'rgb(16,186,204)'
                },
                on: {
                  click: () => {
                    this.activeId = params.row.id
                    this.openMap(params.row)
                  }
                }
              }, '结果查看')
            }
            return h('div', [
              temp,
              h('Button', {
                props: {
                  type: 'primary',
                  size: 'small',
                  icon: 'md-arrow-down'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    let link = document.createElement('a')
                    link.style.display = 'none'
                    let url = '/epr' + params.row.resultUrl.slice(2)
                    link.href = url
                    link.setAttribute('download', url.split('/')[url.split('/').length - 1])
                    document.body.appendChild(link)
                    link.click()
                  }
                }
              }, '下载')
            ])
          }
        }
      ],
      data1: [],
      /* 查询框查询条件 */
      obj: {},
      /* 总条数 */
      total: 0,
      pageNum: 1,
      pageSize: Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48),
      /* 加载 */
      loading: false,
      /* 提取信息框状态 */
      addActive: false,
      /* 添加提取任务的表单数据 */
      addForm: {
        name: '',
        file: '',
        remark: '',
        url: '',
        createDate: ''
      },
      /* 添加提取任务的表单验证 */
      addFormRules: ADDFORMRULES,
      /* 用于控制新增核查任务弹框的变量 */
      modal1: false,
      /* 保存解译任务的id */
      activeId: 0,
      /* 新增任务确认按钮的加载状态 */
      buttonLoading: false,
      // 演示数据变量
      testflag: false,
      showStatus: false,
      spinShow: false
    }
  },
  components: {
    QueryBox,
    // 信息提取地图
    extracTableMap
  },
  computed: {
    // 查询表单
    queryFrom: function () {
      return [
        {
          type: 1,
          field: 'name',
          title: '人类活动提取任务名称'
        }
      ]
    }
  },
  created () {
    this.getTableList()
  },

  mounted () {
    // 窗口改变时图表自适应
    window.addEventListener('resize', () => {
      this.tableHeight = Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40
      this.pageSize = Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48)
      this.getTableList()
    }, false)
  },
  watch: {
    queryData () {
      this.getTableList()
    }
  },
  methods: {
    /* 获取查询数据 */
    getQueryData (data) {
      this.queryData = data
      this.pageNum = 1
      this.getTableList()
    },
    /* 获取字段列表 */
    getTableList () {
      this.loading = true
      listImagerTemp({
        data: {
          pageSize: this.pageSize,
          pageNumber: this.pageNum,
          ...this.queryData
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          if (this.testflag) {
            res.data.data.rows[0].flag = true
          }
          this.data1 = res.data.data.rows
          this.total = res.data.data.total
          this.loading = false
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 分页跳转 */
    pageChange (pageNum) {
      this.pageNum = pageNum
      this.getTableList()
    },
    /* 信息提取地图页面退出 */
    signOut (addActive) {
      this.addActive = addActive
    },
    /* 打开新增窗口 */
    openAddModal () {
      this.modal1 = true
      this.$refs.upload.fileList = []
      this.showStatus = false
      this.addForm.url = ''
      this.addForm.name = ''
      this.addForm.remark = ''
      this.addForm.createDate = ''
      this.$refs.addForm.resetFields()
    },
    /* 打开地图操作 */
    openMap (row) {
      this.addActive = true
      this.$refs.extracTableMap.addTifLayer(row.zipUrl)
      this.$refs.extracTableMap.addShpLayer(row.shpurl.split('/').reverse()[0])
    },
    // 上传图片成功钩子
    uploadSuccess (res, file) {
      if (res.status === 1000) {
        this.spinShow = false
        this.$Notice.success({
          title: '文件上传成功',
          desc: '文件 ' + file.name + ' 上传成功。'
        })
        this.addForm.url = res.data[0].path
        this.showStatus = false
      }
    },
    handleProgress (event, file, fileList) {
      this.spinShow = true
    },
    /* 保存新增人类活动信息提取的表单上传 */
    submitAddForm () {
      this.$refs.addForm.validate(valid => {
        if (valid) {
          if (this.addForm.url === '') {
            this.showStatus = true
          } else {
            this.buttonLoading = true
            // 文件上传成功调用gp服务
            this.extractFeatures(this.addForm.url)
          }
        }
      })
    },
    /**
       *  自动提取人类活动图斑
       */
    async extractFeatures (url) {
      let Geoprocessor = await arcgisPackage.Geoprocessor
      let FeatureSet = await arcgisPackage.FeatureSet
      let gp = new Geoprocessor(config.rasterToShpGP)
      let featureSet = new FeatureSet()
      featureSet.features = [this.graphic]
      if (url) {
        let path = url.substring(0, url.lastIndexOf('/') + 1)
        let outFileName = url.substring(url.lastIndexOf('/') + 1, url.lastIndexOf('.')) // 文件名称
        let params = {
          'raster': url, // 影像数据
          // 'raster': 'E:\\FTP\\dynamicSpace\\GF1B_PMS_E117.9_N31.3_20190907_L1A1227691115-PAN_ortho_fuse_clip.img', // 影像数据
          'numberOfClasses': 5, // 分类级别
          'areaType': '!shape.area@squaremeter!', // 计算面积单位
          'resultTypeExp': '"GRIDCODE" = 3', // 筛选提取后的数据类型
          'outFilePath': config.dynamicLayerSpace, // 解译结果输出路径
          'outFileName': outFileName + '.shp' // 解译结果文件名称
        }
        await gp.submitJob(params).then(result => {
          if (result.jobStatus === 'job-succeeded') {
            // 请求加载接口
            insertImagerTemp({
              data: {
                name: this.addForm.name,
                zipUrl: this.addForm.url,
                remark: this.addForm.remark,
                createDate: renderTime(this.addForm.createDate),
                shpurl: path + outFileName + '.shp',
                // todo 用户名需要更换
                createBy: 1 // 用户id
              }
            }).then(res => {
              if (res.data.code === '0000') {
                this.buttonLoading = false
                this.showStatus = false
                this.modal1 = false
                // 成功提示
                this.$Notice.success({ title: '新建人类活动巡查任务成功' })
              }
            })
          } else {
            this.buttonLoading = false
            // 错误提示
            this.$Notice.error({
              title: '新建人类活动巡查任务失败'
            })
          }
        })
      }

    },
    /* 删除人类活动信息提取任务 */
    proDelete (id) {
      deleteRemoteTask({
        data: {
          id: [id]
        }
      }).then(res => {
        if (res.data.code === '0000') {
          this.getTableList()
          this.$Notice.success({ title: '成功删除人类活动信息提取任务' })
        } else {
          this.$Notice.error({ title: '删除人类活动信息提取任务失败', desc: res.data.msg })
        }
      })
    }
  }
}
</script>

<style lang='scss' scoped rel='stylesheet/scss'>
  #tablet{
    position: relative;
    .iconfont {
      margin-right: 4px;
      &.icon-xuanzewenjian {
        font-size: 12px;
      }
      &.icon-upload {
        font-size: 12px;
      }
    }
    .queryBox {
      height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      position: relative;
      overflow: inherit;
      .queryBtns {
        float: right;
        margin-top: 14px;
      }
    }
    .content_box {
      margin: 0 15px 0;
      height: calc(100% - 65px);
      .page_box{
        width: 100%;
        padding: 15px;
        text-align: right
      }
      .table-box {
        height: calc(100% - 60px);
      }
    }
    .extract_box{
      height: 100%;
      width: 100%;
      position: absolute;
      right: -120%;
      top:0;
      z-index: 1356;
      background: #fff;
      transition: all 0.8s ease;
      border-radius: 0 0 5px 5px;
      .closeBtn{
        position: absolute;
        right:10px;
        bottom:10px;
        z-index: 131531313;
      }
    }
    .addActive{
      right:0;
    }
    .btn-import {
      background-color: rgb(194,146,1);
      border-color: rgb(194,146,1);
      &:hover, &:focus {
        background-color: #B28301;
        border-color: #B28301;
      }
    }
    .btn-add {
      background-color: #01c296;
      border-color: #01c296;
      &:hover, &:focus {
        background-color: #05ab85;
        border-color: #05ab85;
      }
    }
  }
</style>
