<template>
  <div class="contrast" id="contrast">
    <div class="contrast-body">
      <Col span="12" style="float:left;">
        <div class="map1" id="contrast-map1">
          <div id="map1" style="height: 100%"></div>
          <span class="el-select1"  style="color: white" @click='extractFeatures' >原始遥感影像</span>
        </div>
      </Col>
      <Col span="12" style="float: right">
        <div class="map2"  id="contrast-map2">
          <Button type="primary" size="small" class="sign-btn" @click="$emit('signOut',false)" style="position: absolute;top: 15px;right: 15px;z-index: 999">返回</Button>
          <div id="map2" style="height: 100%"></div>
          <span class="el-select1" style="color: white">解译结果</span>
        </div>
      </Col>
    </div>
  </div>
</template>

<script>
// 查询列表
// import { listShp } from '@/api/adjustment'
import arcgisPackage from '../../../utils/arcgisTools'
import config from '../../../setting/mapConfig'
import { tdtlayer, tdtyNoteslayer, tdtclayer, tdtcNoteslayer, redLine, shengjie } from '@/utils/layers'
import Basemapthumbnail from '../../../assets/map_images/Basemapthumbnail.png'
import Basemapthumbnail1 from '../../../assets/map_images/Basemapthumbnail1.png'
export default {
  name: 'contrast',
  data () {
    return {
      map: null,
      /* 红线版本列表数据 */
      periods: []
    }
  },
  props: {
    /* 编辑行主键 */
    imageId: {}
  },
  mounted () {
    this.createMap()
  },
  methods: {
    // 生成地图的函数
    async createMap () {
      let MapView = await arcgisPackage.MapView
      let Map = await arcgisPackage.Map
      let Basemap = await arcgisPackage.Basemap
      let BasemapToggle = await arcgisPackage.BasemapToggle

      // 天地图影像地图
      let tdtylayer = await tdtlayer().then(res => {
        return res
      })
      // 天地图矢量地图
      let tdtylayer1 = await tdtclayer().then(res => {
        return res
      })
      // 天地图的矢量标注图层
      let tdtylayer2 = await tdtcNoteslayer().then(res => {
        return res
      })
      // 天地图影像标注图层
      let tdtylayer3 = await tdtyNoteslayer().then(res => {
        return res
      })
      // 省界
      /*  let layerProvince = await shengjie().then(res => { return res }) */
      /*   let MapImageLayer = await arcgisPackage.MapImageLayer
      let mapimagelayer = new MapImageLayer({
        url: 'http://120.24.69.160:6080/arcgis/rest/services/jshx/testJY2000/MapServer'
      })
      let mapimagelayer2 = new MapImageLayer({
        url: 'http://120.24.69.160:6080/arcgis/rest/services/jshx/testJYResult/MapServer'
      }) */
      let customBasemap = new Basemap({
        baseLayers: [tdtylayer],
        title: '矢量地图',
        id: 'myBasemap',
        thumbnailUrl: Basemapthumbnail
      })

      let customBasemap1 = new Basemap({
        baseLayers: [tdtylayer],
        title: '影像地图',
        id: 'myBasemap1',
        thumbnailUrl: Basemapthumbnail1
      })
      this.map = new Map({
        basemap: customBasemap
      })
      this.map1 = new Map({
        basemap: customBasemap1
      })
      this.view = new MapView({
        map: this.map,
        container: 'map1',
        center: config.centerPoint,
        zoom: 6
      })
      this.view1 = new MapView({
        map: this.map1,
        container: 'map2',
        center: config.centerPoint,
        zoom: 6
      })
      let views = []
      views.push(this.view)
      views.push(this.view1)

      // 设置鼠标滚轮事件
      this.view.on('mouse-wheel', e => {
        // 延迟获取范围的函数
        window.setTimeout(() => {
          // 获取操作图层的范围
          let fullextent = this.view.extent
          // 遍历所有视图来设置这个范围
          views &&
          views.forEach(nview => {
            nview.extent = fullextent
          })
        }, 1000)
      })
      // 设置鼠标拖拽事件
      this.view1.on('drag', e => {
        // 获取操作图层的范围
        let fullextent = this.view1.extent
        // 遍历所有视图来设置这个范围
        views.forEach(nview => {
          nview.extent = fullextent
        })
      })
    },
    /**
       * 添加矢量数据
       * @param url
       * @returns {Promise<void>}
       */
    async addLayer (url) {
      let MapImageLayer = await arcgisPackage.MapImageLayer
      let mapimagelayer = new MapImageLayer({
        url: url
      })
      this.map.add(mapimagelayer)
      mapimagelayer.on('layerview-create', (event) => {
        this.view.extent = mapimagelayer.fullExtent.expand(1.4) // 定位图层
      })
    },
    /**
       * 添加动态工作空间-解译后的矢量数据
       * @param url
       * @returns {Promise<void>}
       */
    async addTifLayer (tifUrl) {
      let MapImageLayer = await arcgisPackage.MapImageLayer
      let layer = MapImageLayer({
        url: config.dynamicSpaceLayer,
        spatialReference: this.view1.spatialReference,
        sublayers: [{
          renderer: {
            type: 'simple',
            visualVariables: [
              {
                type: 'color',
                field: 'Value',
                stops: [
                  { value: 0, color: '#ff0000' },
                  { value: 100, color: '#0000ff' }
                ]
              }
            ]
          },
          source: {
            type: 'data-layer',
            dataSource: {
              type: 'raster',
              workspaceId: 'raster',
              dataSourceName: tifUrl
            }
          }
        }]
      })
      this.map.add(layer)
      layer.on('layerview-create', (event) => {
        layer.fullExtent.spatialReference = this.view.spatialReference
        this.view.extent = layer.fullExtent.expand(1.2) // 定位图层
      })
    },
    /**
       * 添加动态工作空间-解译后的矢量数据
       * @param url
       * @returns {Promise<void>}
       */
    async addShpLayer (shpUrl) {
      let FeatureLayer = await arcgisPackage.FeatureLayer
      let renderer = {
        type: 'simple', // autocasts as new SimpleRenderer()
        symbol: {
          type: 'simple-fill', // autocasts as new SimpleFillSymbol()
          color: [ 255, 0, 0, 1 ],
          outline: { // autocasts as new SimpleLineSymbol()
            width: 1,
            color: 'red'
          }
        }
      }
      let layer = new FeatureLayer({
        url: config.dynamicSpaceLayer,
        spatialReference: this.view1.spatialReference,
        dynamicDataSource: {
          type: 'data-layer',
          dataSource: {
            type: 'join-table',
            leftTableSource: {
              type: 'map-layer',
              mapLayerId: 3
            },
            rightTableSource: {
              type: 'data-layer',
              dataSource: {
                type: 'table',
                workspaceId: 'shp',
                dataSourceName: shpUrl
              }
            },
            leftTableKey: 'STATE_NAME',
            rightTableKey: 'State',
            joinType: 'left-outer-join'
          }
        }
      /*   popupTemplate: {
          title: '图斑详情',
          content: '{*}'
        } */
      })
      this.map1.add(layer)
      layer.on('layerview-create', (event) => {
        layer.fullExtent.spatialReference = this.view1.spatialReference
        this.view1.extent = layer.fullExtent.expand(1.4) // 定位图层
      })
    }
  }
}

</script>

<style lang="scss" scoped>
  .contrast {
    width: 100%;
    height: 100%;
    overflow: hidden;
    .contrast-body {
      width: 100%;
      height: 100%;
      overflow: hidden;
      .ivu-col-span-12 {
        height: 100%;
        width: 49.6% !important;
        .map1,
        .map2 {
          width: 100%;
          height: 100%;
          background: #fff;
          position: relative;

          .el-select1 {
            width: 150px;
            position: absolute;
            top: 15px;
            left: 65px;
          }

          .el-select2 {
            width: 150px;
            position: absolute;
            top: 15px;
            left: 230px;
          }
        }

        .map1 {
          #contrast-map1 {
            width: 100%;
            height: 100%;
          }
        }

        .map2 {
          #contrast-map2 {
            width: 100%;
            height: 100%;
          }
        }
      }

    }

    .contrast-footer {
      height: 155px;
      margin-top: 15px;
    }
  }
  .signB{
    font-weight: bold;
  }
  .red{
    color: #ff0000;
  }
  .green{
    color: #008000;
  }
  #contrast>>>.ivu-table td{
    height: 35px !important;
  }
</style>
